.avatar {
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.title {
  color: #242424;
  font-size: 1.2rem;
  line-height: 1;
  margin-bottom: .5rem;
}

.status {
  line-height: 1;
  color: #aaa;
  margin-bottom: 1rem;
}

.chat {
  width: 100%;
  border: 1px solid #ddd;
  height: 8rem;
  margin-bottom: 1rem;
  overflow-y: auto;
  line-height: 1.2;
  padding: .25rem;
  color: #242424;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  flex-wrap: nowrap;
  overflow-y: auto;
}

.chat:empty::before {
  display: block;
  padding: .5rem;
  content: 'No received messages...';
  color: #ccc;
}

.chat::-webkit-scrollbar {
  width: 4px;
  margin-right: 24px;
}

.chat::-webkit-scrollbar-thumb {
  width: 4px;
  background: #ddd;
}

.message {
  background: #eee;
  margin-top: 0;
  padding: .5rem;
  border-radius: 8px;
}

.message + .message {
  margin-top: .25rem;
}

.message.own {
  align-self: flex-end;
  background: #2980b9;
  color: #fff;
}

.buttons {
  display: flex;
  width: 100%;
}

.buttons button + button {
  margin-left: 1rem;
}
